.catalog {
  padding: 90px 0;
}

.catalog__title {
  margin: 0;
  margin-bottom: 15px;
  font-size: 20px;
  line-height: 24px;
  font-weight: normal;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

@media (min-width: $tablet) {
  .catalog__title {
    position: relative;
    margin-bottom: 50px;
    font-size: 30px;
    line-height: 33px;
    text-align: center;

    &::after {
      content: "";
      position: absolute;
      bottom: -20px;
      left: calc(50% - 43px);
      width: 86px;
      height: 2px;
      background-color: $white;
    }
  }

  .catalog__slider {
    padding: 0 17px;
  }
}

@media (min-width: $desktop) {
  .catalog {
    position: relative;
    box-shadow: 0 -25px 65px 40px $bg-main;


    &::before {
      content: "Каталог услуг";
      position: absolute;
      display: block;
      width: 100%;
      font-size: 190px;
      line-height: 243px;
      color: $white;
      text-transform: uppercase;
      text-align: center;
      opacity: 0.03;
    }
  }

  .catalog__title {
    margin-bottom: 100px;
    font-size: 30px;
    line-height: 33px;
  }

  .catalog__slider {
    padding: 0;
  }
}